@mixin box-shadow($value) {
    -webkit-box-shadow: $value;
    -moz-box-shadow: $value;
    box-shadow: $value;
}

@import '~bootswatch/flatly/_variables.scss';
// External Libs
$bootstrap-sass-asset-helper: false;
// fix relative pack for webpack
$icon-font-path: "~bootstrap-sass/assets/fonts/bootstrap/";
@import '~bootswatch/flatly/_bootswatch.scss';
@import './theme-global.css';

#header-main{
	background:$navbar-inverse-bg;
	color: $navbar-inverse-color;
}
#header-main.navbar{
	border:none;
	margin-bottom:0;
}
#header-main .navbar-toggle .icon-bar{
	background:$navbar-inverse-color;
}

#header-main .dropdown-menu{
	border:none;
}
.body-header-jumbotron.jumbotron, #body-header {
	background:$navbar-inverse-bg;
	color: $navbar-inverse-color;
	margin-bottom:0;
	text-align:center;
}
.body-header-jumbotron.jumbotron.Green-bg{
	background-color:#1abc9c;
}
.body-header-jumbotron.jumbotron.World-bg{
	background-image: url("http://www.publicdomainpictures.net/pictures/130000/velka/businessman-looking-at-town.jpg")
}
#body-header{
	padding:40px 0;
}
#body-header h1{
	font-size: 50px;
    font-weight: 700;
    color: $navbar-inverse-color;
}
#body-header h1 small{
	clear:both;
	color:$headings-color;
	display:block;
	font-size:25px;
	font-weight:normal;
}
#body-main{
	background-color:$body-bg;
}

#body-main h2{
	border-bottom:1px solid $navbar-default-color;
	margin-bottom:20px;
}

#blog-sidenav, .col-sm-3.sidenav{
	margin-top:20px;
}

#blog-sidenav .panel{
	border-radius:0;
	box-shadow:none;
}
#blog-sidenav .panel-heading{
	border-radius:0;
	padding:4px 15px;
}

#blog-sidenav ul,.sidebar-nav ul{
	list-style:none;
	margin:0;
	padding:0;

}

#blog-sidenav ul>li, .sidebar-nav ul>li{
	margin:2px;
	padding: 5px 8px;
}